import { Table, Input } from 'reactstrap'
import Button from 'reactstrap/lib/Button'
import Axios from 'axios'
import { useEffect, useState } from 'react'


const TableHoverAnimation = () => {
  const [tbData, setTbData] = useState()
  const initData = async () => {
    const data = await Axios.get('https://admin.solview.app/trade/initData')
    console.log(data, "datas")
    if (data && data.data && data.data.data) {
      setTbData(data.data.data)
      console.log(data.data.data, "data")
    }
  }
  const changeInput = (value, id) => {
    const tableData = [...tbData]
    for (let i = 0; i < tableData.length; i++) {
      if (tableData[i]._id === id) {
        tableData[i].coinAddress = value
      }
    }
    setTbData(tableData)
  }

  const saveInput = async (id) => {
    const datas = tbData.filter(item => item._id === id)
    const result = await Axios.post('https://admin.solview.app/trade/insertData', { data: datas })
    if (result.data) {
      alert("success!")
    }

  }
  useEffect(() => {
    initData()
  }, [])
  return (
    <Table className='table-hover-animation' responsive>
      <thead>
        <tr>
          <th>Contract Address</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        {(tbData) ? tbData.map((item, key) => (<tr key={key}>
          <td style={{ width: '90%' }}>
            <Input id={item._id} onChange={(e) => changeInput(e.target.value, e.target.id)} value={(item.coinAddress === '0') ? '' : item.coinAddress} placehodler="Please Input contract address"></Input>
          </td>
          <td style={{ width: '10%' }}>
            <Button id={item._id} onClick={(e) => saveInput(e.target.id)}>Save</Button>
          </td>
        </tr>)) : ''
        }
      </tbody>
    </Table>
  )
}

export default TableHoverAnimation
